
import Header from '../common/Header'
import EditInfo from './edit_info'
import Toast from 'vux/src/components/toast/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Loading from 'vux/src/components/loading/index.vue'


export default {
components: {
Toast, Header, Group, XInput, XButton, Selector, Datetime, Cell, Loading
},
data() {
return {
top_title:'',
show: false,
alert_title:'',
text:'',
type:'',
options:[
'',''
],
user: {
birthday: '',
wechat: {
sex: 1
}
},
loadings:true
}
},
methods: {
getInfo() {
axios.post('api/user/info').then(response => {
this.user = response.data.data;
this.loadings = false
}).catch(error => {
this.loadings = false

})
},
getAdmin() {
axios.post('api/user/isAdmin').then(response => {
if(response.data.data.id){
this.is_admin = false
}
}).catch(error => {

})
}
},
mounted() {
this.getInfo()
this.getAdmin()
}
}



