
import Header from '../common/Header'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Selector from 'vux/src/components/selector/index.vue'


export default {
components: {
Header,
Group,
XButton,
XInput,
Datetime,
Selector
},
data() {
return {
top_title: '',
options: {
sex: [{ key: 1, value: '' }, { key: 2, value: '' }]
},
user:{
name:'',
mobile:'',
birthday:'',
sex:'',
email:''
},
age:'',
finall_user:{}

}
},
methods:{
saveMes(){
if (this.validator()) {
axios.post('api/user/modify', this.finall_user).then(response => {
this.$vux.alert.show({
content: response.data.message
})
this.$router.push('/user_info')
}).catch(error => {
this.$vux.alert.show({
content: error.response.data.message
})
})
} else {
this.$vux.alert.show({
content: ''
})
}
},
getInfo() {
axios.post('api/user/info').then(response => {
this.user = response.data.data
})
},
validator() {
if(this.user.name != '' && this.user.mobile != '' && this.user.birthday != ''
&& this.user.sex != '' && this.user.email != null) {
this.finall_user['name'] = this.user.name
this.finall_user['mobile'] = this.user.mobile
this.finall_user['birthday'] = this.user.birthday
this.finall_user['sex'] = this.user.sex
this.finall_user['email'] = this.user.email
return true
} else {
return false
}
}
},
mounted() {
this.getInfo()
}
}



